import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs'
import { Profile } from '../models/Profile';

@Injectable()
export class ProfileService {
  Profile: Profile
  Profiles: Profile[]
  private url = "/api/profile"

  constructor(private http: HttpClient) { }

  allProfile(): Observable<Profile[]> {
    return this.http.get<Profile[]>(this.url + '/all')
  }
  currentProfile(): Observable<Profile> {
    return this.http.get<Profile>(this.url, {
      headers: new HttpHeaders().set('Authorization', this.Token)
    })
  }
  postProfile(Profile) {
    return this.http.post(this.url, Profile, {
      headers: new HttpHeaders().set('Authorization', this.Token)
    })
  }
  getProfileByHandle(handle) {
    return this.http.get(`${this.url}/handle/${handle}`)
  }
  postExperience(exp) {
    return this.http.post(this.url + '/experience', exp, {
      headers: new HttpHeaders().set('Authorization', this.Token)
    })
  }
  postEducation(edu) {
    return this.http.post(this.url + '/education', edu, {
      headers: new HttpHeaders().set('Authorization', this.Token)
    })
  }
  deleteExperience(id) {
    return this.http.delete(this.url + '/experience/:' + id, {
      headers: new HttpHeaders().set('Authorization', this.Token)
    })
  }
  deleteEducation(id) {
    return this.http.delete(this.url + '/education/:' + id, {
      headers: new HttpHeaders().set('Authorization', this.Token)
    })
  }
  deleteAccount() {
    return this.http.delete(this.url, {
      headers: new HttpHeaders().set('Authorization', this.Token)
    })
  }
  get Token() {
    return localStorage.getItem('token')
  }

}
