import { Component, OnInit } from '@angular/core';
import { ProfileService } from 'src/app/service/profile.service';
import { Router } from '@angular/router';
import { Profile } from 'src/app/models/Profile';


@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  isLoading: Boolean = true
  errors = {}
  userProfile: Profile
  // There is no need to declare user Object but 
  //i am getting a undifined error while parsing 'profile.user.name'
  // so to not get this error i am declaring a user object
  user = {}
  experience = []
  education = []

  constructor(
    private profileService: ProfileService,
    private router: Router) { }

  ngOnInit() {
    this.profileService.currentProfile()
      .subscribe(
        profile => {
          this.userProfile = profile
          this.user = profile.user
          this.experience = profile.experience
          this.education = profile.education
          this.Spinner()
        },
        err => {
          this.Spinner()
          this.errors = err.error
        })
  }
  createProfile() {
    this.router.navigate(['/createProfile'])
  }

  deleteExp(id) {
    this.Spinner()
    this.profileService.deleteExperience(id)
      .subscribe(() => {
        this.reloadPage()
        this.Spinner()
      })
  }
  deleteEdu(id) {
    this.Spinner()
    this.profileService.deleteEducation(id)
      .subscribe(() => {
        this.reloadPage()
        this.Spinner()
      })
  }

  deleteACC() {
    this.profileService.deleteAccount()
      .subscribe(() => {
        localStorage.removeItem('token')
        this.router.navigate(['/login'])
      }, err => {
        console.log(err)
      })
  }

  Spinner() {
    this.isLoading = !this.isLoading
  }
  reloadPage() {
    window.location.reload()
  }

}
