import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HttpClientModule } from '@angular/common/http'
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FlashMessagesModule } from 'angular2-flash-messages';


import { AppComponent } from './app.component';
import { LoginComponent } from './components/login/login.component';
import { SignupComponent } from './components/signup/signup.component';
import { NavBarComponent } from './components/nav-bar/nav-bar.component';
import { NotFoundComponent } from './components/not-found/not-found.component';
import { UserService } from './service/user.service';
import { ProfilesComponent } from './components/profiles/profiles.component';
import { ProfileComponent } from './components/profile/profile.component';
import { ProfileService } from './service/profile.service';
import { AddEducationComponent } from './components/add-education/add-education.component';
import { AddExperienceComponent } from './components/add-experience/add-experience.component';
import { CreateProfileComponent } from './components/create-profile/create-profile.component';
import { EditProfileComponent } from './components/edit-profile/edit-profile.component'
import { PostComponent } from './components/posts/post.component';
import { PostsService } from './service/posts.service';
import { SpinnerComponent } from './components/spinner/spinner.component';
import { SinglePostComponent } from './components/single-post/single-post.component';
import { LandingComponent } from './components/landing/landing.component';
import { ViewProfileComponent } from './components/view-profile/view-profile.component'
import { AuthGuard } from './auth/auth.guard';





const routes: Routes = [
  { path: '', component: LandingComponent },
  { path: 'login', component: LoginComponent },
  { path: 'register', component: SignupComponent },
  { path: 'all', component: ProfilesComponent },
  { path: 'current', component: ProfileComponent, canActivate: [AuthGuard] },
  { path: 'profile/:handle', component: ViewProfileComponent },
  { path: 'editProfile', component: EditProfileComponent, canActivate: [AuthGuard] },
  { path: 'createProfile', component: CreateProfileComponent, canActivate: [AuthGuard] },
  { path: 'addEducation', component: AddEducationComponent, canActivate: [AuthGuard] },
  { path: 'addExperience', component: AddExperienceComponent, canActivate: [AuthGuard] },
  { path: 'posts', component: PostComponent, canActivate: [AuthGuard] },
  { path: 'post/:id', component: SinglePostComponent, canActivate: [AuthGuard] },
  { path: '**', component: NotFoundComponent }
]

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    SignupComponent,
    NavBarComponent,
    NotFoundComponent,
    ProfilesComponent,
    ProfileComponent,
    AddEducationComponent,
    AddExperienceComponent,
    CreateProfileComponent,
    EditProfileComponent,
    PostComponent,
    SpinnerComponent,
    SinglePostComponent,
    LandingComponent,
    ViewProfileComponent,
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule.forRoot(routes, { onSameUrlNavigation: 'reload' }),
    FlashMessagesModule.forRoot(),
  ],
  providers: [
    AuthGuard,
    UserService,
    ProfileService,
    PostsService],
  bootstrap: [AppComponent]
})
export class AppModule { }
