import { Component, OnInit } from '@angular/core';
import { ProfileService } from 'src/app/service/profile.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-view-profile',
  templateUrl: './view-profile.component.html',
  styleUrls: ['./view-profile.component.css']
})
export class ViewProfileComponent implements OnInit {
  isLoading: Boolean = true
  profile: any
  handle
  skill: any = []
  experience: any = []
  education: any = []
  user: any = {}
  constructor(
    private profileService: ProfileService,
    private activatedRoute: ActivatedRoute) {
    this.handle = this.activatedRoute.snapshot.params['handle']
  }

  ngOnInit() {
    this.profileService.getProfileByHandle(this.handle)
      .subscribe(profile => {
        this.profile = profile
        this.user = this.profile.user
        this.skill = this.profile.skills
        this.experience = this.profile.experience
        this.education = this.profile.education
        this.isLoading = false
      }, err => {
        this.isLoading = false
      })
  }

}
